import React from 'react';
import ReactDOM from 'react-dom';
import { View, Panel, PanelHeader, PanelHeaderContent, Div,platform, FixedLayout, Tabs, TabsItem, IOS, ANDROID } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Entry from './Entry';
import User from './User';

const osname = platform();

class Navigation extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: 'entry',
      fetchedUser: this.props.fetchedUser,
    };
  }

  renderSwitch(param) {
    switch(param) {
      case 'entry':
        return <Entry />;
      case 'user':
        return <User />
      default:
        console.log(this.state.activeTab);
      }
  }

  render() {
    return (
        <Panel id="navigation" >
          <PanelHeader noShadow>
            <PanelHeaderContent
        //status="Тюменская область"
      >
        Центр "Мои Документы"<br/>Тюменская область
      </PanelHeaderContent>
          </PanelHeader>
          {this.renderSwitch(this.state.activeTab)}
          <FixedLayout vertical="bottom">
            <Tabs theme="light" >
              <TabsItem
                onClick={() => this.setState({ activeTab: 'entry'})}
                selected={this.state.activeTab === 'entry'}>
                Запись
              </TabsItem>
              <TabsItem
                onClick={() => this.setState({ activeTab: 'user'})}
                selected={this.state.activeTab === 'user'}>
                Личный кабинет
              </TabsItem>
            </Tabs>
          </FixedLayout>
        </Panel>
    );
  }
}

export default Navigation;

///({osname === IOS ? 'IOS': 'Android'})



